.mainimage {
  position: relative;
}

.bord {
  background-color: rgb(255, 0, 98);
}
.bord p {
  color: white;
  text-align: center;
  display: block;
  margin: 0 auto;
}

.maintext {
  position: absolute;
  color: rgb(255, 0, 98);
  font-size: 4em;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
}

.wardimg {
  width: 300px;
  height: 300px;
  margin: 0;
  padding: 0;
  display: inline-block;
}
.wardtx {
  display: inline-block;
  vertical-align: top;
}
.acsub {
  display: inline-block;
  vertical-align: top;
  margin-left: 20px;
  font-family: sans-serif;
  color: rgb(92, 92, 92);
}

.actext {
  font-family: sans-serif;
  color: rgb(255, 0, 140);
}

.acp {
  padding-top: 10px;
  padding-left: 20px;
  font-family: sans-serif;
  font-weight: bold;
  color: rgb(59, 59, 59);
}

.accord {
  margin: 0 100px;
}

.box {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 60px;
  color: rgb(126, 126, 126);
}

.tab {
  padding-top: 80px;
}
